export default {
  name: 'Submenu',
  data() {
    return {
      showMenu: false,
      curURL: '',
      curOrigin: '',
      langPath: ''
    }
  },
  mounted() {
    this.curURL = window.location.href
    this.curOrigin = window.location.origin
    this.langPath = '/' + this.$i18n.locale
  },
  props: {
    menuItems: {
      type: Object,
      required: true
    }
  },
  methods: {
    handleClose() {
      this.showMenu = false
    }
  }
}
